.top-spacer {
  height: 85vh;
}

.bottom-spacer {
  height: 110vh;
}

.spacer {
  height: 13vh;
	background: transparent;
}

.sticky {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.header {
  display: flex;
  justify-content: left;
  opacity: 1;
  background-color: white;
}

.header_link {
	z-index: 101;
  padding: 20px;
  color: darkgoldenrod;
  background: white;
	opacity: 1;
  font-weight: bold;
  border: none;
  border-bottom: 3px solid transparent;
  cursor: pointer;
  outline: none;
}

.selected {
  border-bottom: 3px solid darkgoldenrod;
  color: darkgoldenrod;
}

.section-1 {
	position: relative;
	background-image: url("wmbike_blur.png");
	width: 100%;
	height: 75vh;
	opacity: 1;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.section-2 {
	background-image: url("lumboblurred.png");
	width: 100%;
	height: 85vh;
	opacity: 1;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.section-3 {
	background-image: url("presentation_blurred.png");
	width: 100%;
	height: 95vh;
	opacity: 1;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}


.headshot {
	position: absolute;
	opacity: 1;
