
.fade-in-section {
  opacity: 0.0;
  visibility: hidden;
  transition: opacity 1.0s ease-out, transform 0.5s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  visibility: visible;
	position: relative;
	top: 10%;
	left: 5%;
	width: 90%;
	height: 80%;
	background-color: white;
}
